import React, { useEffect, useState } from "react";
import { w3cwebsocket as W3CWebSocket } from "websocket";
import { useNavigate } from "react-router-dom";
import LoadingProduct from "../../assets/icons/loadingProduct.svg";
import { useMessage } from '../../Components/MessageContext';
import { toast } from "react-toastify";

interface Item {
  message: string;
  isSuccess: boolean | null;
  type: string | null;
}

type BarcodeType = "seriNo" | "chargePointId";

interface WebSocketComponentProps {
    handleAddItem: (message: string, isSuccess: boolean | null, type?: string | null, step?: number, fontSize?: string) => void;
}

const WebSocketComponent: React.FC<WebSocketComponentProps> = ({ handleAddItem }) => {
  const [isConnecting, setIsConnecting] = useState(true);
  const navigate = useNavigate();
  const [waitingForBarcode, setWaitingForBarcode] = useState(false);
  const [barcode, setBarcode] = useState("");
  const [mcu_error, setmcu_error] = useState("");
  // const [timeoutId, setTimeoutId] = useState<NodeJS.Timeout | null>(null);
  const [debounceId, setDebounceId] = useState<NodeJS.Timeout | null>(null);
  const [expectedBarcodeType, setExpectedBarcodeType] = useState<BarcodeType | null>(null);

  const {
    socket,
    setSocket,
    USBList,
    setUSBList,
    wifiSSID,
    setwifiSSID,
    wifiPassword,
    setwifiPassword,
    fourG_apn,
    setfourG_apn,
    fourG_user,
    setfourG_user,
    fourG_password,
    setfourG_password,
    fourG_pin,
    setfourG_pin,
    selectedUSB, setSelectedUSB,
    items,
    setItems, timeoutId, setTimeoutId,
    containerRef,
    LOADBANK_I1, setLOADBANK_I1,
    LOADBANK_I2, setLOADBANK_I2,
    LOADBANK_I3, setLOADBANK_I3,
    LOADBANK_V1, setLOADBANK_V1,
    LOADBANK_V2, setLOADBANK_V2,
    LOADBANK_V3, setLOADBANK_V3,
    LOADBANK_P1, setLOADBANK_P1,
    LOADBANK_P2, setLOADBANK_P2,
    LOADBANK_P3, setLOADBANK_P3,
    isStep1Complete, setIsStep1Complete,
    isStep2Complete, setIsStep2Complete,
    isStep3Complete, setIsStep3Complete,
    isStep4Complete, setIsStep4Complete,
    isStep5Complete, setIsStep5Complete,
    isStep6Complete, setIsStep6Complete,
    isStep7Complete, setIsStep7Complete,
    isStep8Complete, setIsStep8Complete,
  } = useMessage();

  useEffect(() => {
    console.log("Socket state gncellendi:", socket);
  }, [socket]);

  const send_ac_charger_connect_request = (socket : W3CWebSocket) => {
    if (socket) {
      if (socket.readyState === socket.OPEN) {
        socket.send(
          JSON.stringify({
            Command: "ACChargerConnectRequest",
            Data: "",
          })
        );
      } else {
        toast.error("Server'a balanlamyor!");
      }
    } else {
      toast.error("Server'a balanlamyor!");
    }
  }

  const handleBarcodeInput = (event: KeyboardEvent) => {
    // console.log("event.key", event.key, "expectedBarcodeType", expectedBarcodeType)
    if (!expectedBarcodeType) return;

    if (event.key === "Shift") return;

    if (event.key === "") {
      setBarcode((prev) => prev + ".");
      return;
    }

    if (event.key === "Enter") {
      if (debounceId) clearTimeout(debounceId);
      processBarcode();
      if (timeoutId) clearTimeout(timeoutId);
    } else {
      setBarcode((prev) => prev + event.key);
      if (debounceId) clearTimeout(debounceId);
      // const newDebounceId = setTimeout(() => console.log("Debounced barcode:", barcode), 300);
      // setDebounceId(newDebounceId);
    }
  };

  const sendBarcode = (command: string) => {
    if (socket) {
      if (socket.readyState === socket.OPEN) {
        socket.send(
          JSON.stringify({
            Command: command,
            Data: barcode,
          })
        );
      } else {
        toast.error("Server'a balanlamyor!");
      }
    } else {
      toast.error("Server'a balanlamyor!");
    }
  };

  const processBarcode = () => {
    if (barcode.trim() === "") {
      handleAddItem("Barkod girilmedi. Hata!", false);
    } else if (expectedBarcodeType === "seriNo") {
      handleAddItem(`Cihaz Seri No: ${barcode}`, true, null, 1);
      setIsStep1Complete(true)
      sendBarcode("SeriNoBarcode");
      handleAddItem("Cihazn Model Idsi sorgulanyor...", null, null, 2);
      setBarcode("");
    } else if (expectedBarcodeType === "chargePointId") {
      if (barcode.startsWith("HCAC") && barcode.length === 10) {
        handleAddItem(`Charge Point ID: ${barcode}`, true, null, 3);
        setIsStep3Complete(true)
        sendBarcode("ChargePointIdBarcode");
        setBarcode("");
      } else {
        toast.error(`Geersiz barkod! : ${barcode}`)
        handleAddItem("Ltfen cihazn Charge Point Id barkodunu okutunuz!", null, null, 3);
        setBarcode("");
        setWaitingForBarcode(true);
      }
    }
  };

  const startTimeout = () => {
    // console.log("startTimeout")
    if (timeoutId) clearTimeout(timeoutId);
    const id = setTimeout(() => {
      // console.log("setTimeout")
      if (expectedBarcodeType) {
        // console.log("expectedBarcodeType")
      }
      if (!barcode) {
        handleAddItem("Barkod girilmedi. Hata!", false);
      }
    }, 3600000);
    setTimeoutId(id);
  };


  useEffect(() => {
    if (waitingForBarcode) {
      window.addEventListener("keydown", handleBarcodeInput);
      startTimeout();
    } else {
      window.removeEventListener("keydown", handleBarcodeInput);
      if (timeoutId) clearTimeout(timeoutId);
    }

    return () => {
      window.removeEventListener("keydown", handleBarcodeInput);
      if (timeoutId) clearTimeout(timeoutId);
    };
  }, [waitingForBarcode, barcode]);


  const Tick = () => {
    return <img className="tick" src="/assets/img/tik.png" alt="" />
  };


  const connectWebSocket = () => {
    const newSocket = new W3CWebSocket('ws://' + window.location.hostname + ':4000');

    newSocket.onopen = () => {
        console.log("WebSocket balants kuruldu.");
        setIsConnecting(false);
    };

    newSocket.onmessage = (message) => {
        try {
            const jsonData = JSON.parse(message.data.toString());
            if (jsonData.Command != "LoadData" && jsonData.Command != "USBList"){
              console.log(jsonData)
            }
            
            switch (jsonData.Command) {
              case "LoadData":
                setLOADBANK_I1(jsonData.Data.LOADBANK_I1)
                setLOADBANK_I2(jsonData.Data.LOADBANK_I2)
                setLOADBANK_I3(jsonData.Data.LOADBANK_I3)
                setLOADBANK_V1(jsonData.Data.LOADBANK_V1)
                setLOADBANK_V2(jsonData.Data.LOADBANK_V2)
                setLOADBANK_V3(jsonData.Data.LOADBANK_V3)
                setLOADBANK_P1(jsonData.Data.LOADBANK_P1)
                setLOADBANK_P2(jsonData.Data.LOADBANK_P2)
                setLOADBANK_P3(jsonData.Data.LOADBANK_P3)
                break
              case "USBList":
                setUSBList(jsonData.Data)
                break
              case "Config":
                //handleAddItem("SMART FONKSIYON ve BALANTI TESTLER", null, "header", 1);
                if (jsonData.Data) {
                  const newSelectedUSB = jsonData.Data.selectedUSB || "";
                  console.log("Config'den gelen USB:", newSelectedUSB);
                  setSelectedUSB(newSelectedUSB);
                  setwifiSSID(jsonData.Data.wifiSSID || "");
                  setwifiPassword(jsonData.Data.wifiPassword || "");
                  setfourG_apn(jsonData.Data.fourG_apn || "");
                  setfourG_user(jsonData.Data.fourG_user || "");
                  setfourG_password(jsonData.Data.fourG_password || "");
                  setfourG_pin(jsonData.Data.fourG_pin || "");
                }
                break
              case "USBControl":
                if (jsonData.Data) {
                  toast.success("Test cihazna baland.")
                  setExpectedBarcodeType("seriNo")
                }
                else {
                  toast.error("Test cihazna balanamad!")
                }
                break
              case "SeriNoRequest":
                handleAddItem("Ltfen cihazn seri numara barkodunu okutunuz!", null, null, 1);
                setWaitingForBarcode(true);
                break
              case "SeriNoBarcodeResult":
                if (jsonData.Data) {
                  if (jsonData.Data.result) {
                    const deviceInfo = [
                      `ItemCode: ${jsonData.Data.ItemCode}\n`,
                      `emergencyButton: ${jsonData.Data.emergencyButton}\n`,
                      `midMeter: ${jsonData.Data.midMeter}\n`,
                      `system: ${jsonData.Data.system}\n`,
                      `bodyColor: ${jsonData.Data.bodyColor}\n`,
                      `connectorType: ${jsonData.Data.connectorType}\n`,
                      `caseType: ${jsonData.Data.caseType}\n`
                    ].join('');
                    handleAddItem(deviceInfo, null, null, 2, "20px");
                    setIsStep2Complete(true)
                  }
                  else {
                    toast.error("SAP sisteminde bir sorun olutu!")
                    //handleAddItem("SAP sisteminde bir sorun olutu!", false, null, null, "14px");
                  }
                }
                break
              case "ChargePointIdRequest":
                setBarcode("")
                setExpectedBarcodeType("chargePointId")
                handleAddItem("Ltfen cihazn Charge Point Id barkodunu okutunuz!", null, null, 3);
                setWaitingForBarcode(true);
                break
              case "WaitDevice":
                handleAddItem("Test cihaznn hazr olmas bekleniyor...", null, null, 4);
                break
              case "WaitDeviceResult":
                handleAddItem("Test cihaz hazr.", true, null, 4);
                setIsStep4Complete(true)
                send_ac_charger_connect_request(newSocket)
                handleAddItem("AC Charger'a balanlyor...", null, null, 5);
                break
              case "ACChargerConnectResult":
                if(jsonData.Data){
                  handleAddItem("AC Charger'a baland.", true, null, 5);
                  setIsStep5Complete(true)
                } else {
                  handleAddItem("AC Charger'a balanlamad!", false, null, 5);
                }
                break
              case "MasterCardRequest":
                handleAddItem("Ltfen master kat cihaza okutunuz!", null, null, 6);
                break
              case "MasterCardResult":
                if (jsonData.Data == "" || jsonData.Data == null){
                  handleAddItem("Master kart alnamad " + jsonData.Data, false, null, 6);
                }
                else{
                  handleAddItem("Master kart kayt edildi:" + jsonData.Data, true, null, 6);
                }
                break
              case "User1CardRequest":
                handleAddItem("Ltfen birinci kullanc katn cihaza okutunuz!", null)
                break
              case "User2CardRequest":
                handleAddItem("Ltfen ikinci kullanc katn cihaza okutunuz!", null)
                break
              case "User1CardResult":
                if (jsonData.Data == "Same"){
                  handleAddItem("Ayn kart okuttunuz!", false)
                }
                else if (jsonData.Data == "" || jsonData.Data == null){
                  handleAddItem("Birinci kullanc kart alnamad " + jsonData.Data, false)
                }
                else{
                  handleAddItem("Birinci kullanc kayt edildi:" + jsonData.Data, true)
                }
                break
              case "User2CardResult":
                if (jsonData.Data == "Same"){
                  handleAddItem("Ayn kart okuttunuz!", false)
                }
                else if (jsonData.Data == "" || jsonData.Data == null){
                  handleAddItem("kinci kullanc kart alnamad " + jsonData.Data, false)
                }
                else{
                  handleAddItem("kinci kullanc kayt edildi: " + jsonData.Data, true)
                }
                break
              case "WaitConfigResult":
                handleAddItem("AC arj cihaznn 4G'li model ise 4G'ye balanmas bekleniyor...", null)
                handleAddItem("AC arj cihaznn Wifiye balanmas bekleniyor...",null)
                handleAddItem("AC arj cihaznn Bluetooth ad charge point id ile deitiriliyor...",null)
                handleAddItem("AC arj cihaznn Ethernet MAC Adresi alnyor...",null)
                handleAddItem("AC arj cihaznn MCU hata durumu sorgulanyor...",null)
                handleAddItem("AC arj cihaznn 4G'li model ise 4G imei numaras alnyor...", null)
                break
              case "ACChargerNotConnected":
                handleAddItem("AC arj cihazndan cevap alnamad!", false)
                break
              case "SaveConfigResult":
                if (jsonData.Data.bluetooth_mac == "" || jsonData.Data.bluetooth_mac == null){
                  handleAddItem("Bluetooth mac adresi alnamad!", false)
                } else {
                  handleAddItem("Bluetooth mac adresi alnd: " + jsonData.Data.bluetooth_mac, true)
                }
                if (jsonData.Data.eth_mac == "" || jsonData.Data.eth_mac == null){
                  handleAddItem("Ethernet mac adresi alnamad!", false)
                } else {
                  handleAddItem("Ethernet mac adresi alnd: " + jsonData.Data.eth_mac, true)
                }
                if (jsonData.Data.mcu_error.length != 0){
                  setmcu_error("")
                  jsonData.Data.mcu_error.forEach((error: string, index: number) => {
                    handleAddItem("MCU'da hata var! " + error, false)
                  });
                  
                } else {
                  handleAddItem("MCU'da hata yok.", true)
                }
                if (jsonData.Data.mcu_connection == false) {
                  handleAddItem("MCU bal deil!", false)
                } else {
                  handleAddItem("MCU bal.", true)
                }
                if (jsonData.Data.fourg == true) {
                  if (jsonData.Data.imei_4g == "" || jsonData.Data.imei_4g == null){
                    handleAddItem("4G imei adresi alnamad!", false)
                  } else {
                    handleAddItem("4G imei adresi alnd: " + jsonData.Data.imei_4g , true)
                  }
                }
                if (jsonData.Data.wlan0_connection == false) {
                  handleAddItem("Wifi'ye balanamad!", false)
                } else {
                  handleAddItem("Wifi'ye baland.", true)
                }
                break
              case "ChargeTest":
                handleAddItem("ARJ TEST BALADI", null, "header", 2);
                handleAddItem("Ltfen kablonun takl olduundan emin olunuz!", null)
                break
              case "DeviceB":
                if(jsonData.Data){
                  handleAddItem("arj cihaz B konumuna getirildi", true)
                } else{
                  handleAddItem("arj cihaz B konumuna getirilemedi", true)
                }
                break
              case "WaitUser1CardRequest":
                handleAddItem("Ltfen birinci kullanc RFID kartn okutunuz!", null)
                break
              case "AgainTest":
                handleAddItem("Test ileminde hata var ltfen kontrol edip tekrar test ediniz!", false)
                break
              case "WaitUser1CardResult":
                if(jsonData.Data){
                  handleAddItem("Birinci kullanc kart okutuldu", true)
                } else{
                  handleAddItem("Birinci kullanc kart okutulamad!", false)
                }
                break
              case "WaitUser2CardResult":
                if(jsonData.Data){
                  handleAddItem("kinci kullanc kart okutuldu", true)
                } else{
                  handleAddItem("kinci kullanc kart okutulamad!", false)
                }
                break
              case "WaitRelayOnRequest":
                handleAddItem("Rlenin On olmas bekleniyor...", null)
                break
              case "WaitRelayOnResult":
                if(jsonData.Data){
                  handleAddItem("Rle On oldu. Yk bankas devrede.",true)
                } else{
                  handleAddItem("Rle On olmad!",false)
                }
                break
              case "ControlVoltageRequest":
                handleAddItem("Voltaj deerleri 5 saniye boyunca kontrol ediliyor...",null)
                break
              case "ControlVoltage":
                if(jsonData.Data){
                  handleAddItem("Voltaj deerleri kontrol edildi doruland.",true)
                } else{
                  handleAddItem("Voltaj deerleri snr amtr! Test durduruldu.",false)
                }
                break
              case "ControlCurrentRequest":
                handleAddItem("Akm deerleri 5 saniye boyunca kontrol ediliyor...",null)
                break
              case "ControlCurrent":
                if(jsonData.Data){
                  handleAddItem("Akm deerleri kontrol edildi doruland.",true)
                } else{
                  handleAddItem("Akm deerleri snr amtr! Test durduruldu.",false)
                }
                break
              case "ControlAllValues30sn":
                handleAddItem("30 sn ierisinde de test cihaznn ve arj cihaznn okuduu akm, gerilim ve g deerleri karlatrlr...",null)
                break
              case "ControlAllValues30snResult":
                if(jsonData.Data){
                  handleAddItem("Akm, Voltaj ve G deerleri kontrol edildi doruland.",true)
                } else{
                  handleAddItem("Akm, Voltaj ve G deerleri snr amtr! Test durduruldu.",false)
                }
                break
              case "OverCurrentTest":
                handleAddItem("AIRI AKIM TEST", null, "header")
                break
              case "OverCurrentTestResult":
                setmcu_error("")
                if (jsonData.Data.length != 0){
                  jsonData.Data.forEach((error: string, index: number) => {
                    handleAddItem("MCU'da hata: " + error, true)
                  });
                }
                else{
                  handleAddItem("MCU'da hata yok!" , false)
                }
                break
              case "RCDLeakageCurrentSet":
                handleAddItem("arj Cihaznn RCD hatasna gemesi bekleniyor..." , null)
                break
              case "RCDLeakageCurrentTestResult":
                if (jsonData.Data.length != 0){
                  jsonData.Data.forEach((error: string, index: number) => {
                    handleAddItem("MCU'da hata: " + error, true)
                  });
                }
                else{
                  handleAddItem("MCU'da hata yok!" , false)
                }
                break
              case "WaitAState":
                handleAddItem("arj cihaznn 5 sn ierisinde A state'ine gemesi bekleniyor..." , null)
                break
              case "WaitAStateResult":
                if(jsonData.Data){
                  handleAddItem("arj cihaz A state'ine geti.",true)
                } else{
                  handleAddItem("arj cihaz 5 sn ierisinde A statine geemedi!",false)
                }
                break
              case "WaitCState":
                handleAddItem("arj cihaznn 40 sn ierisinde C state'ine gemesi bekleniyor..." , null)
                break
              case "WaitCStateResult":
                if(jsonData.Data){
                  handleAddItem("arj cihaz tekrar C state'ine geti.",true)
                } else{
                  handleAddItem("arj cihaz 40 sn ierisinde C statine geemedi!",false)
                }
                break
              case "RCDLeakageCurrentTest":
                handleAddItem("RCD KAAK AKIM TEST", null, "header")
                break
              case "SecondUserCard":
                handleAddItem("KNC RFID KARTININ TEST", null, "header")
                handleAddItem("arj cihaz B statine getirildi.",true)
                // handleAddItem("arj Balatmak in kinci Kullanc RFID Kartn Okutunuz!",null)
                break
              case "WaitUser2CardRequest":
                handleAddItem("Ltfen birinci kullanc RFID kartn okutunuz!", null)
                break
              case "SecondUserWaitCStateResult":
                if(jsonData.Data){
                  handleAddItem("arj cihaz tekrar C state'ine geti.",true)
                } else{
                  handleAddItem("arj cihaz C state'ine geemedi!",false)
                }
                break
              case "EndTest":
                handleAddItem("TESTN SONLANDIRILMASI", null, "header")
                break
              case "SapResult":
                if(jsonData.Data){
                  handleAddItem("arj cihaz SAP sistemine baarl kayt edildi.",true)
                } else{
                  handleAddItem("arj cihaz SAP sistemine baarl kayt edilemedi!",false)
                }
                break
      
                // Dier case'ler...
            }
        } catch (error) {
            console.error("Mesaj ileme hatas:", error);
        }
    };

    newSocket.onerror = (error) => {
        console.error("WebSocket hatas:", error);
        setIsConnecting(false);
    };

    newSocket.onclose = () => {
        console.log("WebSocket balants kapand");
        setIsConnecting(false);
        setTimeout(() => connectWebSocket(), 1000);
    };

    setSocket(newSocket);
    return newSocket;
  };

  useEffect(() => {
    const ws = connectWebSocket();

    return () => {
        if (ws.readyState === ws.OPEN) {
            ws.close();
        }
    };
  }, []);

  return <div></div>;
};

export default WebSocketComponent;